
import Spinner from 'vux/src/components/spinner/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'

import api from '../api/api'
import auth from '../api/auth'
import env from '../config/env'
export default {
    data() {
        return {
            id: -1,
            order: {},
            showLoading: false,
            loadingType: 'lines'
        }
    },
     mounted() {
        this.id = this.$route.params.id
        this.initData()
    },
    methods: {
        async initData() {
            let vm = this
            // 显示加载动画
            vm.showLoading = true
            // 根据产品id获取订单详细信息
            api.get('getOrderInfo/' + vm.id).then((res) => {
                if(res.status===1){
                    // 订单对象
                    vm.order = res.data
                }else{
                    vm.$vux.alert.show({
                        title: env.alertInfo,
                        content: res.message,
                        buttonText: '确定',
                        dialogTransition:'',
                        maskTransition:''
                    })
                }
                // 隐藏加载动画
                vm.showLoading = false
            }).catch(error => {
                vm.$vux.alert.show({
                    title: env.alertInfo,
                    content: env.errorInfo,
                    buttonText: '确定',
                    dialogTransition:'',
                    maskTransition:''
                })
            })
        },
        back() {
            this.$router.replace({path: '/myOrder'});
        }
    },
    components: {
        Spinner,
        XHeader
    }
}



